const router = useRouter();
const vector = new VectorLayer({
    source: new VectorSource(),
});



// ResizeObserver
const debounce = (callback, delay) => {
    let tid;
    return function (...args) {
        const ctx = self;
        tid && clearTimeout(tid);
        tid = setTimeout(() => {
            callback.apply(ctx, args);
        }, delay);
    };
